
  <script lang="ts">
    import format from "@aduh95/format-phone-number";

    import Social from "./Social.svelte";

    export let user;
    export let emailAddress;

    const companyAddress =
      "e-Comas Sarl, 68 Avenue de la Liberté, 1930 Luxembourg";

    const { Name, Title, Phone, pictureUrl } = user;
    const url = user.url ?? {};

    const decodeImgFromUrl = (url) => {
      const img = new Image();
      img.src = url;
      return img.decode().then(() => img);
    };
    const IMG_HEIGHT = 105;

    const data = Promise.all([
      Phone ? format(Phone) : Promise.resolve(),
      decodeImgFromUrl(pictureUrl).then(
        (img) => img.naturalWidth * (IMG_HEIGHT / img.naturalHeight),
        (e) => {
          console.error(e);
          return IMG_HEIGHT;
        }
      ),
    ]);
  </script>
{#await data}
<p>...loading</p>
{:then [PhoneInternationalFormat, IMG_WIDTH]}
<table id="signature" cellpadding="0" cellspacing="0" style="mso-table-lspace: 0pt; mso-table-rspace: 0pt; width: 640px; padding: 0; font-family: Arial, Helvetica, sans-serif; -webkit-font-smoothing: antialiased; font-size: 14px; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; border-collapse: separate;">
