              <tbody>
                <tr>
                  <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                    <img alt={Name + "'s picture"} width={IMG_WIDTH} height={IMG_HEIGHT} src={pictureUrl} style="-ms-interpolation-mode: bicubic; max-width: 100%; height: auto; border: none; padding-right: 5px;">
                  </td>

                  <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                    <table cellpadding="0" cellspacing="5" style="mso-table-lspace: 0pt; mso-table-rspace: 0pt; border-collapse: separate;">
                      <tbody>
                        <tr>
                          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                            <strong class="name" style="color: #51bf9d;">{Name}</strong>
                          </td>
                        </tr>
                        <tr>
                          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                            <strong style="color: #20262b;">{Title}</strong>
                          </td>
                        </tr>
                        <tr>
                          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                            <a href={"mailto:" + emailAddress} target="_blank" style="color: #20262b; text-decoration: none;">{emailAddress}</a>
                          </td>
                        </tr>
                        {#if Phone}
                        <tr>
                          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">
                            <a href={"tel:" + Phone} target="_blank" style="color: #20262b; text-decoration: none;">{PhoneInternationalFormat}</a>
                          </td>
                        </tr>
                        {/if}
                        <tr>
                          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;">{companyAddress}</td>
                        </tr>
                      </tbody>
                    </table>
                  </td>
                </tr>
              </tbody>
            </table>
          </td>
        </tr>
        <tr>
          <td style="font-family: Arial, Helvetica, sans-serif; font-size: 14px; vertical-align: top;"><Social links={url} style="display: flex; gap: 5px;"></Social></td>
